// Funo para buscar pas por cdigo
export const findCountryByCode = (code) => {
  return countryCodes.find(country => country.code === code) || countryCodes[0];
};

// Funo para formatar telefone com cdigo do pas
export const formatPhoneNumber = (countryCode, phone) => {
  if (!phone) return '';
  return `${countryCode} ${phone}`;
};

// Funo para validar nmero de telefone bsico
export const validatePhoneNumber = (phone) => {
  // Remove espaos e caracteres especiais
  const cleanPhone = phone.replace(/[\s\-\(\)\.]/g, '');
  
  // Verifica se contm apenas nmeros
  if (!/^\d+$/.test(cleanPhone)) {
    return false;
  }
  
  // Verifica se tem pelo menos 8 dgitos e no mximo 15
  return cleanPhone.length >= 8 && cleanPhone.length <= 15;
};